@use 'modules/core';
@use "modules/components/indicator";

$_indicator-position: ("bottom": 20%, "right": 14%);
$_indicator_size: 45%;

:host {
  display: block;
}

.soul-avatar {
  @include core.flexbox-inline-row();
  align-items: center;
  vertical-align: middle;
  width: 100%;
}

.soul-avatar__thumbnail {
  width: var(--_soul-avatar-thumbnail-size, #{core.$soul-space-unit-xl});
  height: var(--_soul-avatar-thumbnail-size, #{core.$soul-space-unit-xl});
  color: var(--_soul-avatar-thumbnail-color, #{core.$soul-color-text-on-dark});
  user-select: none;
  cursor: inherit;
  position: relative;
  flex-shrink: 0;
}

.soul-avatar__icon,
.soul-avatar__chars,
.soul-avatar__image {
  @include core.flexbox-row();
  @include core.flexbox-center-horizontally-and-vertically();
  width: 100%;
  height: 100%;
  box-shadow: core.$soul-shadow-border-thin;
  border-radius: var(--_soul-avatar-thumbnail-border-radius, #{core.$soul-border-radius-circle});
}

.soul-avatar__image {
  object-fit: cover;
  object-position: center center;
  background-color: core.$soul-color-background-primary; //To fix jagged border radius/box-shadow
}

.soul-avatar__icon,
.soul-avatar__chars {
  background-color: var(--_soul-avatar-thumbnail-background-color, #{core.$soul-color-brand-primary-500});
}

.soul-avatar__chars {
  white-space: nowrap;
  text-transform: uppercase;
  overflow: hidden;
  font-size: var(--_soul-avatar-chars-font-size);
  line-height: var(--_soul-avatar-chars-line-height);
}

.soul-avatar__icon {
  soul-icon {
    --soul-icon-size: var(--_soul-avatar-icon-size, #{core.get-font-size(core.$soul-2-xs )});
  }
}

.soul-avatar__content {
  @include core.flexbox-column();
  justify-content: center;
  margin: var(--_soul-avatar-content-margin, #{core.$soul-space-inline-left-2-xs});
  overflow: hidden;
}

.soul-avatar__additional-text,
.soul-avatar__status-message {
  font-size: var(--_soul-avatar-additional-text-font-size);
  line-height: var(--_soul-avatar-additional-text-line-height);
  @include core.truncate();
}

.soul-avatar__additional-text {
  color: var(--_soul-avatar-additional-text-color, #{core.$soul-color-text-reduced-on-light});
}

.soul-avatar__name {
  color: var(--_soul-avatar-name-color, currentColor);
  font-size: var(--_soul-avatar-name-font-size);
  line-height: var(--_soul-avatar-name-line-height);
  @include core.truncate();
}

.soul-avatar__indicator {
  bottom: map-get($_indicator-position, "bottom");
  right: map-get($_indicator-position, "right");
  transform: translate(50%, 50%);
  position: absolute;
  width: $_indicator_size;
  height: $_indicator_size;
}

soul-indicator {
  --soul-indicator-size: 100%;
}

.soul-avatar-color--primary {
  --_soul-avatar-thumbnail-background-color: #{core.$soul-color-background-avatar-primary};
}

.soul-avatar-color--neutral {
  --_soul-avatar-thumbnail-background-color: #{core.$soul-color-background-avatar-neutral};
}

$_avatar-colors: (
  core.$soul-color-background-avatar-1,
  core.$soul-color-background-avatar-2,
  core.$soul-color-background-avatar-3,
  core.$soul-color-background-avatar-4,
  core.$soul-color-background-avatar-5,
  core.$soul-color-background-avatar-6,
  core.$soul-color-background-avatar-7,
  core.$soul-color-background-avatar-8,
  core.$soul-color-background-avatar-9,
  core.$soul-color-background-avatar-10,
  core.$soul-color-background-avatar-11,
  core.$soul-color-background-avatar-12,
  core.$soul-color-background-avatar-13
);

@for $index from 1 through length($_avatar-colors) {
  $avatar-color: nth($_avatar-colors, $index);
  .soul-avatar-color--#{$index} {
    --_soul-avatar-thumbnail-background-color: #{$avatar-color};
  }
}

:host([status="warning"]) {
  .soul-avatar__status-message {
    color: var(--_soul-avatar-status-message-color, #{core.$soul-color-text-indicator-warning});
  }
}

:host([status="positive"]) {
  .soul-avatar__status-message {
    color: var(--_soul-avatar-status-message-color, #{core.$soul-color-text-indicator-positive});
  }
}

:host([status="waiting"]),
:host([status="locked"]) {
  .soul-avatar__status-message {
    color: var(--_soul-avatar-additional-text-color, #{core.$soul-color-text-reduced-on-light});
  }
}

:host([muted]:not([muted="false"])) {
  .soul-avatar {
    --_soul-avatar-thumbnail-background-color: #{core.$soul-color-background-disabled};
    --_soul-avatar-additional-text-color: #{core.$soul-color-text-disabled};
    --_soul-avatar-status-message-color: #{core.$soul-color-text-disabled};
    --_soul-avatar-name-color: #{core.$soul-color-text-disabled};
    --_soul-avatar-thumbnail-color: #{core.$soul-color-text-disabled};
  }

  .soul-avatar__image,
  .soul-avatar__indicator, {
    filter: grayscale(100%);
    opacity: core.$opacity-l;
  }
}

:host([type='entity']) {
  .soul-avatar {
    --_soul-avatar-thumbnail-border-radius: #{core.$soul-border-radius-m};
  }
}

:host([size='s']) {

  &:host([type='entity']) {
    .soul-avatar {
      --_soul-avatar-thumbnail-border-radius: #{core.$soul-border-radius-s};
    }
  }

  .soul-avatar {
    --_soul-avatar-thumbnail-size: #{core.$soul-space-unit-m};
    --_soul-avatar-content-margin: #{core.$soul-space-inline-left-2-xs};
    --_soul-avatar-chars-font-size: #{core.get-font-size(core.$soul-2-xs)};
    --_soul-avatar-chars-line-height: #{core.get-line-height(core.$soul-2-xs)};
    --_soul-avatar-additional-text-font-size: #{core.get-font-size(core.$soul-xs)};
    --_soul-avatar-additional-text-line-height: #{core.get-line-height(core.$soul-2-xs)};
    --_soul-avatar-name-font-size: #{core.get-font-size(core.$soul-s)};
    --_soul-avatar-name-line-height: #{core.get-line-height(core.$soul-s)};
    --_soul-avatar-icon-size: #{core.get-font-size(core.$soul-2-xs)};
  }
}

:host,
:host([size='m']) {
  .soul-avatar {
    --_soul-avatar-thumbnail-size: #{core.$soul-space-unit-xl};
    --_soul-avatar-content-margin: #{core.$soul-space-inline-left-xs};
    --_soul-avatar-chars-font-size: #{core.get-font-size(core.$soul-s)};
    --_soul-avatar-chars-line-height: #{core.get-line-height(core.$soul-s)};
    --_soul-avatar-additional-text-font-size: #{core.get-font-size(core.$soul-xs)};
    --_soul-avatar-additional-text-line-height: #{core.get-line-height(core.$soul-xs)};
    --_soul-avatar-name-font-size: #{core.get-font-size(core.$soul-s)};
    --_soul-avatar-name-line-height: #{core.get-line-height(core.$soul-s)};
    --_soul-avatar-icon-size: #{core.get-font-size(core.$soul-m)};
  }
}

:host([size='l']) {
  .soul-avatar {
    --_soul-avatar-thumbnail-size: #{core.$soul-space-unit-2-xl};
    --_soul-avatar-content-margin: #{core.$soul-space-inline-left-s};
    --_soul-avatar-chars-font-size: #{core.get-font-size(core.$soul-l)};
    --_soul-avatar-chars-line-height: #{core.get-line-height(core.$soul-l)};
    --_soul-avatar-additional-text-font-size: #{core.get-font-size(core.$soul-m)};
    --_soul-avatar-additional-text-line-height: #{core.get-line-height(core.$soul-m)};
    --_soul-avatar-name-font-size: #{core.get-font-size(core.$soul-l)};
    --_soul-avatar-name-line-height: #{core.get-line-height(core.$soul-l)};
    --_soul-avatar-icon-size: #{core.get-font-size(core.$soul-xl)};
  }
}

:host([size='xl']) {
  .soul-avatar {
    --_soul-avatar-thumbnail-size: #{2 * core.$soul-space-unit-2-xl};
    --_soul-avatar-content-margin: #{core.$soul-space-inline-left-m};
    --_soul-avatar-chars-font-size: #{core.get-font-size(core.$soul-3-xl)};
    --_soul-avatar-chars-line-height: #{core.get-line-height(core.$soul-3-xl)};
    --_soul-avatar-additional-text-font-size: #{core.get-font-size(core.$soul-2-xl)};
    --_soul-avatar-additional-text-line-height: #{core.get-line-height(core.$soul-2-xl)};
    --_soul-avatar-name-font-size: #{core.get-font-size(core.$soul-3-xl)};
    --_soul-avatar-name-line-height: #{core.get-line-height(core.$soul-3-xl)};
    --_soul-avatar-icon-size: #{core.get-font-size(core.$soul-3-xl)};
  }
}
